<template>
  <div 
    class="my-input"
    :class="`${size}`"
  >
      <input 
        :type="type"
        :id="id"
        :name="id"
        :class="`${align}`"
        v-model="input"
        autocomplete="off"
        @keyup.enter="sendInput"
        required
      >
      <label for="">{{ label }}</label>
      <span 
        class="clear-btn"
        @click="clearInput">
          X
      </span>
      <span 
        class="underline"
        :class="`${bdrColor}`"
      >
      </span>
  </div>
</template>

<script>
export default {
    name : 'myInputTxt',
    props : {
        id : {
            type : String,
            default : 'id'
        },
        align : {
            type : String,
            default : 'left'
        },
        size : {
            type : String,
            default : 'md',
        },
        bdrColor : {
            type : String,
            default : 'pink'
        },
        label : {
            type : String,
            default : 'No name'
        },
        value : {
            type : String,
            default : ""
        },
        type : {
            type : String,
            default : 'text',
        }
    },
    data() {
        return {
            input : this.value,
        }
    },
    methods : {
        sendInput() {
            return this.$emit('onInput',this.input);
        },
        clearInput() {
            this.input = "";
        },

        testCoba() {
            console.log('jalan slueee')
        }
    }
}
</script>

